@import "mixins";
@import "vars";

$testimonials-height: 1025px;
$image-size: 140px;

.testimonials {
	height: $testimonials-height;
	padding: 148px 226px 0;
	
	&__header-small {
		@include header(13px);
		text-transform: uppercase;
		color: $text-lighter;
	}
	
	&__header-big {
		color: $text-medium;
		@include header(32px);
		margin: 16px 0 85px;
	}
	
	&__cards {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	
	&__card {
		background-color: #fff;
		width: 314px;
		height: 494px;
		border-radius: 24px;
		box-shadow: 0px 9px 18px rgba(37, 41, 49, 0.03);
		
		@include hover (transform, scale(1.3));
		
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-between;
		
		padding: 87px 32px 32px;
		
		&--blue {
			@include testimonials-card ($color-blue,1);
		}
		
		&--yellow {
			@include testimonials-card ($color-yellow,2);
		}
		
		&--red {
			@include testimonials-card ($color-red,3);
		}
	}
	
	&__image {
		height: $image-size;
		width: $image-size;
		border-radius: 50%;
		margin-bottom: 82px;
		transition: all 2s ease-in-out;
		
		&:hover {
			@include spinaroo;
		}
	}
	
	&__icon {
		width: 21px;
		height: 19px;

	}
	
	&__text {
		@include text(13px);
		color: $text-light;
		font-weight: 600;
		text-align: center;
	}
	
	&__name {
		@include text(13px);
		color: $text-medium;
		font-weight: 600;
	}
	
	&__company {
		@include header(13px);
		color: $text-lighter;
		text-transform: uppercase;
		letter-spacing: 3px;
	}
}
